import {onMounted,watch,onUnmounted} from "vue"
import Article from "../../../../store/Article.ts"

export default function(UserInput,select_ul){
  const titlereg = /^[\u.4e00-\u9fa5A-Za-z]{2,30}$/
  let ulElement = null
  const use_Article = Article()

  let WatchID = watch(UserInput,newValue => {
            if(titlereg.test(newValue)){
              ulElement.style.color="#00c851"
              //在这里发送请求
              use_Article.get_SelectArticle(newValue)
            }else{
              ulElement.style.color="#f44336"
            }
            !newValue && (ulElement.style.color="#333333")
          })


  onMounted(()=>{
    ulElement = select_ul.value
  })
  onUnmounted(()=>{
    WatchID()
  })

}