import {onMounted} from 'vue'
import {useRouter} from "vue-router"

export default function(exit_button){

  let exit_buttonElement = null
  const router = useRouter()


function getSelectArticle(id){
  exit_buttonElement.click()
  setTimeout(()=>{
    router.replace(`/articles/${id}`)
  },1000)
}

  onMounted(()=>{
    exit_buttonElement = exit_button.value
  })

  return {getSelectArticle}
}